@import '../variables';

.categories {
  flex: 1;
  max-width: 80%;
  overflow: auto;

  @media (max-width: 768px) {
    max-width: 100%;
    margin-bottom: 30px;
  }

  ul {
    display: flex;

    li {
      background-color: #F2DFD3;
      color: $brown;
      padding: 13px 30px;
      border-radius: 30px;
      margin-right: 10px;
      font-weight: bold;
      cursor: pointer;
      transition: background-color 0.1s ease-in-out;
      @include noselect();

      &:hover {
        background-color: darken(#F2DFD3, 2%);
      }

      &:active {
        background-color: darken(#F2DFD3, 5%);
      }

      &.active {
        background-color: #735441; 
        color: #F2DFD3;
      }
    }
  }
}
